import type { ConvertToPluginActionCreators } from '@ali/4ever-cangjie';

export const INDENT_INCREASE = Symbol('INDENT_INCREASE');

export const INDENT_DECREASE = Symbol('INDENT_DECREASE');

export function increaseIndent() {
  return {
    type: INDENT_INCREASE,
  };
}

export function decreaseIndent() {
  return {
    type: INDENT_DECREASE,
  };
}


export const actions = {
  increaseIndent,
  decreaseIndent,
};

/** @deprecated */
export const DEPRECATED_ACTIONS = {
  INDENT_INCREASE,
  INDENT_DECREASE,

  increaseIndent,
  decreaseIndent,
};

declare module '@ali/4ever-cangjie' {
  interface PluginActionCreators extends ConvertToPluginActionCreators<typeof actions> {}
}
